<template>
  <div id="app">
    <component :is="layout">
      <transition name="fade" mode="out-in">
        <router-view></router-view>
      </transition>
    </component>
  </div>
</template>

<script>
const default_layout = "default";

export default {
  computed: {
    layout() {
      return (this.$route.meta.layout || default_layout) + "-layout";
    }
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      fetch(this.$config.api_base + "auth/fastapi", {
        method: "GET",
        mode: "no-cors",
        credentials: "include"
      })
        .then(data => data.json())
        .then(data => {
          if (data.code == 200) {
            this.$store.user.username = data.username;
            this.$store.user.email = data.email;
            this.$store.user.qq = data.qq;
            this.$store.user.iconUrl =
              "https://q2.qlogo.cn/headimg_dl?dst_uin=" +
              this.$store.user.qq +
              "&spec=100&url_enc=0&referer=bu_interface&term_type=PC";
            this.$store.user.cb = 0;
            this.$store.user.islogin = true;
          }
        })
        .catch(error => {
          // TODO
        });
    }
  }
};
</script>

<style lang="scss">
@import "assets/base.scss";
</style>
